@keyframes showing {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/opensanslight.woff2), url(../fonts/opensanslight.woff);
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/opensans.woff2), url(../fonts/opensans.woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/opensansbold.woff2), url(../fonts/opensansbold.woff);
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.dot {
  border: 2px dashed tomato;
}
.page__body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
}
* {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.contacts {
  padding: 50px 20px 25px;
}
@media (min-width: 1200px) {
  .contacts {
    padding: 70px 130px 25px;
  }
}
.contacts__text {
  margin: 0;
  margin-bottom: 45px;
  font-size: 24px;
  font-weight: 300;
  text-align: center;
}
@media (min-width: 660px) {
  .contacts__text {
    margin-bottom: 50px;
    font-size: 40px;
    line-height: 55px;
  }
}
@media (min-width: 1200px) {
  .contacts__text {
    margin-bottom: 60px;
    font-size: 50px;
    line-height: 68px;
  }
}
.contacts__group-wrapper {
  width: 250px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .contacts__group-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
  }
}
.contacts__group-item {
  text-align: center;
}
@media (min-width: 660px) and (max-width: calc(1200px - 1px)) {
  .contacts__group-item:last-child {
    width: 100%;
    flex-grow: 1;
  }
}
@media (min-width: 1200px) {
  .contacts__group-item {
    margin-left: 5%;
  }
  .contacts__group-item:first-child {
    order: -2;
  }
  .contacts__group-item:last-child {
    order: -1;
  }
}
.contacts__group-item:not(:last-child) {
  margin-bottom: 30px;
}
.contacts__caption {
  margin: 0;
  font-size: 18px;
  font-weight: 700;
}
.contacts__phone,
.contacts__email,
.contacts__adress {
  margin: 0;
  font-size: 24px;
  font-weight: 300;
  text-decoration: none;
  color: #d22856;
}
.contacts__phone:hover,
.contacts__email:hover,
.contacts__adress:hover {
  text-decoration: underline;
  color: #283645;
}
.contacts__phone:active,
.contacts__email:active,
.contacts__adress:active {
  opacity: 0.3;
  text-decoration: none;
}
.effect {
  padding: 50px 20px 0;
}
@media (max-width: calc(660px - 1px)) {
  .effect {
    display: none;
  }
}
@media (min-width: 1200px) {
  .effect {
    padding: 75px 130px;
    background-color: #f2f2f2;
  }
}
.effect-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.effect-content__pic {
  position: relative;
  width: 238px;
  height: 276px;
  background-image: url(../img/icon-timer.svg);
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-width: 1200px) {
  .effect-content__pic {
    width: 298px;
    height: 345px;
  }
}
.effect-content__duration {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: block;
  margin: auto;
  width: 110px;
  height: 75px;
  font-size: 96px;
  font-weight: 700;
  line-height: 110%;
  text-align: center;
  color: #d22856;
}
@media (min-width: 1200px) {
  .effect-content__duration {
    width: 138px;
    height: 94px;
    font-size: 120px;
    line-height: 115%;
  }
}
.effect-content__info {
  width: 50%;
  min-height: 200px;
  padding-left: 2.4%;
}
@media (min-width: 1200px) {
  .effect-content__info {
    width: 56%;
    padding: 0;
  }
}
.effect-content__caption {
  margin-bottom: 15px;
  font-size: 30px;
  font-weight: 700;
  line-height: 36px;
  color: #000000;
}
@media (min-width: 1200px) {
  .effect-content__caption {
    margin-bottom: 30px;
  }
  .effect-content__caption br {
    display: none;
  }
}
.effect-content__text {
  margin-bottom: 15px;
  line-height: 24px;
}
@media (min-width: 1200px) {
  .effect-content__text {
    margin-bottom: 50px;
    font-size: 18px;
    line-height: 30px;
  }
}
.effect-content__link {
  line-height: 24px;
  color: #d22856;
}
.effect-content__link:hover {
  color: #283645;
}
.effect-content__link:active {
  opacity: 0.3;
}
@media (min-width: 1200px) {
  .effect-content__link {
    font-size: 18px;
    line-height: 30px;
  }
}
.features {
