.gallery {
  padding: 0 20px;
}
@media (min-width: 1200px) {
  .gallery {
    padding: 0 130px;
  }
}
.gallery__wrapper {
  margin-bottom: 45px;
}
@media (min-width: 660px) {
  .gallery__wrapper {
    margin-bottom: 65px;
  }
}
.gallery__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (min-width: 660px) {
  .gallery__list {
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: minmax(455px, auto);
    gap: 20px;
  }
}
.gallery__item {
  background-color: #f2f2f2;
}
.gallery__item--header {
  position: relative;
}
@media (min-width: 660px) {
  .gallery__item--header {
    grid-column: 1/3;
    grid-row: 1/2;
