}
.gallery__item--header::before {
  content: "Панорама дня";
  position: absolute;
  top: 0;
  right: 0;
  width: 140px;
  height: 36px;
  font-size: 12px;
  font-weight: 700;
  line-height: 36px;
  text-transform: uppercase;
  text-align: center;
  vertical-align: middle;
  color: #ffffff;
  background-color: #d22856;
}
@media (min-width: 660px) {
  .gallery__item--header::before {
    top: 50px;
    right: 20px;
  }
}
.card__image {
